import React from "react";
import '../css/MainBtn.css';

const MainBtn = (props) => {

    let showAgeBlock = () => {
        let className = 'active';
        props.showAgeBlock(className);
    }

    return (
        <button className={'main__btn' + ' ' + props.state.hidden + ' ' + props.state.className} onClick={showAgeBlock}>
            <span>{props.state.btnTitle}</span>
            <span className="icon-down-chevron"></span>
        </button>
    )
}

export default MainBtn;