import React from "react";
import '../../css/FirstPage.css';
import MainBtn from "../../MainBtn/MainBtnConponent";

const FirstPage = (props) => {
    let pageInput = React.createRef();

    let inputChildName = () => {
        let childName = pageInput.current.value;
        let buttonState = '';
        props.childNameChange(childName, buttonState);
    }

    return (
        <div className="wrapper check__page">
            <div className="left__block page__1">
                <h1 className="page__title">
                    Help Us Personalize Your Child's Learning Journey!
                </h1>
                <p className="page__text">
                    Providing this info helps us pick content that's just right.
                </p>
                <p className="question__text">
                    What's your child's first name?
                </p>
                <input type="text" className="name__input" value={props.state.firstPageState.inputState.inputVal}
                       onChange={inputChildName} ref={pageInput}
                       placeholder="Child's first name"/>
                <MainBtn state={props.state.buttonsState.nextBtn} showAgeBlock={props.showAgeBlock}/>
                <div className={'second__main__block' + ' ' + props.state.firstPageState.chooseAgeBlock.className}>
                    <p className="question__text">
                        How old is <span id='child_name'></span>?
                    </p>
                    <div className="age__table">
                        <div className="age__block">
                            <button className="age__btn">
                                1
                            </button>
                        </div>
                        <div className="age__block">
                            <button className="age__btn">
                                2
                            </button>
                        </div>
                        <div className="age__block">
                            <button className="age__btn">
                                3
                            </button>
                        </div>
                        <div className="age__block">
                            <button className="age__btn">
                                4
                            </button>
                        </div>
                        <div className="age__block">
                            <button className="age__btn">
                                5
                            </button>
                        </div>
                        <div className="age__block">
                            <button className="age__btn">
                                6
                            </button>
                        </div>
                        <div className="age__block">
                            <button className="age__btn">
                                7
                            </button>
                        </div>
                        <div className="age__block">
                            <button className="age__btn">
                                8+
                            </button>
                        </div>
                    </div>
                </div>
            </div>
            <div className="right__block page__1">

            </div>
        </div>
    )
}

export default FirstPage;