import React from "react";
import '../css/StepWidget.css';

const StepWidget = (props) => {
    return (
        <aside className='step__widget'>
            <div className={"step__number__block" + ' ' + props.state.step1.className}>
                <span className="step__number">1</span>
            </div>
            <div className={'short__step__line' + ' ' + props.state.step1.className}></div>
            <div className={'long__step__line' + ' ' + props.state.step2.className}></div>
            <div className={"step__number__block" + ' ' + props.state.step2.className}>
                <span className="step__number">2</span>
            </div>
            <div className={'short__step__line' + ' ' + props.state.step2.className}></div>
            <div className={'long__step__line' + ' ' + props.state.step3.className}></div>
            <div className={"step__number__block" + ' ' + props.state.step3.className}>
                <span className="step__number">3</span>
            </div>
        </aside>
    )
}

export default StepWidget;