import React from "react";
import Header from "../Header/HeaderComponent";
import StepWidget from "../StepWidget/StepWidgetComponent";
import FirstPage from "./FirstPage/FirstPageComponent";


const CheckLearnerPage = (props) => {
    return (
        <div className={'component__body'}>
            <Header/>
            <div className="wrap">
                <StepWidget state={props.state.stepWidgetState}/>
                <FirstPage state={props.state} childNameChange={props.childNameChange} showAgeBlock={props.showAgeBlock}/>
            </div>
        </div>
    )
}

export default CheckLearnerPage;