import {rerenderEntireTree} from "../index";

let store = {
    state: {
        stepWidgetState: {
            step1: {
                id: 1,
                className: 'active'
            },
            step2: {
                id: 2,
                className: ''
            },
            step3: {
                id: 3,
                className: ''
            }
        },
        buttonsState: {
            nextBtn: {
                id: 1,
                className: 'next__btn',
                hidden: 'hidden',
                btnTitle: 'Next'
            }
        },
        firstPageState: {
            inputState: {
                inputVal: '',
            },
            chooseAgeBlock: {
                className: '',
            },
        }
    }
}

export let inputValeChangeAC = (val, buttonState) => {
    store.state.firstPageState.inputState.inputVal = val;
    store.state.buttonsState.nextBtn.hidden = buttonState;
    rerenderEntireTree(store);
}
export let chooseAgeBlockShowAC = (className) => {
    store.state.firstPageState.chooseAgeBlock.className = className;
    rerenderEntireTree(store);
}

export default store;